import { createApp } from 'vue'
import App from './App.vue'
import PrimeVue from 'primevue/config';
import InputText from 'primevue/inputtext';
import Button from 'primevue/button';
import DataTable from 'primevue/datatable';
import Column from 'primevue/column';
import Toast from 'primevue/toast';
import Dialog from 'primevue/dialog';
import ConfirmPopup from 'primevue/confirmpopup';
import ProgressSpinner from 'primevue/progressspinner';
import RadioButton from 'primevue/radiobutton';
import Badge from 'primevue/badge';
import HlsVideo from "./components/HLSVideo.vue";
import { library } from '@fortawesome/fontawesome-svg-core'
import { faStop, faPlay, faTrash, faEye, faSync } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
library.add(faStop, faPlay, faTrash, faEye, faSync)


import ConfirmationService from 'primevue/confirmationservice';
import ToastService from 'primevue/toastservice';

import 'primevue/resources/themes/arya-blue/theme.css';
import 'primevue/resources/primevue.min.css';
import 'primeicons/primeicons.css';
import router from './router'

const app = createApp(App).use(router);

app.use(PrimeVue);
app.use(ToastService);
app.use(ConfirmationService);
app.component('font-awesome-icon', FontAwesomeIcon)

app.component('HlsVideo', HlsVideo);
app.component('Badge', Badge);
app.component('ProgressSpinner', ProgressSpinner);
app.component('ConfirmPopup', ConfirmPopup);
app.component('InputText', InputText);
app.component('Button', Button);
app.component('DataTable', DataTable);
app.component('Column', Column);
app.component('Toast', Toast);
app.component('Dialog', Dialog);
app.component('RadioButton', RadioButton)
app.mount('#app');


window.destinations = {
    localDomain: '192.168.0.135',
    localPort: '8000',
    localWsPort: '3030',
    remoteDomain: 'flyingbits.ddns.net',
    remotePort: '13370',
    remoteWsPort: '13371',
    devDomain: 'localhost',
    devPort: '8000',
    devWsPort: '3030',
    activeDomain: null,
    activePort: null,
    activeWsPort: null
}

const urlParams = new URLSearchParams(window.location.search);
const env = urlParams.get('env');
if (env == 'internet') {
    window.destinations.activeDomain = window.destinations.remoteDomain;
    window.destinations.activePort = window.destinations.remotePort;
    window.destinations.activeWsPort = window.destinations.remoteWsPort;
} else if (env == 'dev') {
    window.destinations.activeDomain = window.destinations.devDomain;
    window.destinations.activePort = window.destinations.devPort;
    window.destinations.activeWsPort = window.destinations.devWsPort;
} else {
    window.destinations.activeDomain = window.destinations.localDomain;
    window.destinations.activePort = window.destinations.localPort;
    window.destinations.activeWsPort = window.destinations.localWsPort;
}